*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  height: 100vh;
  width: 100vw;
  background: #c5b2fa;
}

.app {
  width: 28%;
  margin: 0 auto;
  background: linear-gradient(rgb(17, 21, 29), #2f2e3e);
  height: 98vh;
  border-radius: 20px;
  opacity: 0;
  animation: 0.7s fade-in ease-in forwards;
}
@keyframes fade-in {
  100% {
    opacity: 1;
  }
}

@media all and (max-width: 540px) {
  .app {
    width: 90%;
  }
}
